import { Component, OnInit } from '@angular/core';
import { ContentService } from '../core/content.service';
import { User } from '../core/user';

declare interface TableData {
  headerRow: string[];
}

@Component({
  selector: 'app-total-users',
  templateUrl: './total-users.component.html',
  styleUrls: ['./total-users.component.css']
})
export class TotalUsersComponent implements OnInit {

  public tableData: TableData;
  public usersList: User[];

  constructor(
    private contentService: ContentService
  ) { }

  ngOnInit() {
    this.tableData = {
      headerRow: ['ID', 'Provider', 'Name', 'Email', 'Role', 'City', 'Region']
    };
    this.contentService.getUsersData().snapshotChanges().subscribe(item => {
      this.usersList = [];
      item.forEach(element => {
        const y = element.payload.toJSON();
        y['uid'] = element.key;
        this.usersList.push(y as User);
      });
    });
  }
}
